import React from "react";
import io from "socket.io-client";
import YurucomiClient from "../yurucomiClient";
export default class Events extends React.Component {
    constructor(props) {
        super(props);
        this.socket = io(location.origin);
        this.state = { eventList: [], reconnecting: false };
        this.connect = this.connect.bind(this);
    }
    connect() {
        const yurucomiClient = new YurucomiClient();
        yurucomiClient.listen(this.props.groupName, this.props.userName);
        yurucomiClient.watch(event => {
            const newList = [
                {
                    from: event._from,
                    tuple: event._payload,
                    fromImage: event._fromIcon,
                },
                ...this.state.eventList,
            ];
            this.setState({ eventList: newList });
        });
    }
    componentDidMount() {
        this.connect();
    }
    render() {
        return (React.createElement("div", { className: "events" }, this.state.eventList.map((value) => {
            return (React.createElement("div", { className: "event-child" },
                React.createElement("div", { className: "from-icon" },
                    React.createElement("img", { src: value.fromImage, alt: "" })),
                React.createElement("div", { className: "tuple-area" },
                    React.createElement("div", { className: "tuple" }, JSON.stringify(value.tuple)))));
        })));
    }
}
