import React from "react";
import io from "socket.io-client";
export default class UserPage extends React.Component {
    constructor(props) {
        super(props);
        this.socket = io(location.origin);
        this.state = { watchingList: {}, reconnecting: false };
        this.getWatching = this.getWatching.bind(this);
    }
    validate(nameArray, myName) {
        const result = nameArray.filter(n => n !== myName);
        return result;
    }
    getWatching() {
        const setting = JSON.parse(localStorage.getItem(this.props.groupName) || "{}");
        const settingArray = Object.entries(setting);
        this.setState({ watchingList: settingArray });
    }
    componentDidMount() {
        //this.connect();
        // this.getWatching();
    }
    componentDidUpdate() { }
    render() {
        return (React.createElement("div", { className: "events" },
            React.createElement("div", null, "aaaaaa")));
    }
}
